.custom-input,
.custom-textarea {
  position: relative;

  display: flex;

  label {
    display: flex;
    flex-direction: column;
    width: 100%;

    input,
    textarea {
      position: relative;

      flex-grow: 1;
      min-height: 50px;
      padding: 10px;

      border: 1px solid rgba(0, 26, 84, 0.2);
      border-radius: 10px;
      outline: none;

      transition: border $trans-default;

      // &:hover {
      //   border: 2px solid rgba($color-default-black, 0.5);
      // }

      &:focus {
        color: $color-sapphire;

        border: 1px solid $color-sapphire;
      }
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: -22px;

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    opacity: 0;

    transition: opacity $trans-default;

    &-description {
      position: absolute;
      top: calc(100% - 28px);
      right: calc(-50% + 27px);
      z-index: 1;

      width: 100%;

      text-align: center;

      opacity: 0;

      @include vp-767 {
        right: 0;
        left: 0;

        display: flex;
        justify-content: flex-end;
      }

      &::before {
        content: "";
        position: absolute;
        top: -16px;
        left: calc(50% - 6px);

        color: $color-default-white;

        border: 10px solid transparent;
        border-top: 10px solid $color-pumpkin;
        transform: rotate(180deg);

        @include vp-767 {
          left: calc(100% - 37px);
        }
      }
    }

    &-text {
      display: inline-flex;
      max-width: 200px;
      margin: 0;
      padding: 4px 10px;

      font-size: 10px;
      line-height: 14px;
      text-align: center;
      color: $color-default-white;

      background: $color-pumpkin;
      border-radius: 10px;
    }
  }

  &.is-invalid {
    textarea,
    input {
      border: 1px solid $color-pumpkin;

      &:focus,
      &:hover {
        border: 1px solid $color-pumpkin;
      }
    }

    .custom-input__error {
      opacity: 1;

      &-svg {
        position: absolute;
        right: 13px;
        bottom: 37px;

        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;

        background-color: $color-pumpkin;
        border-radius: 50%;
        cursor: pointer;

        @include vp-767 {
          right: 17px;
          bottom: 35px;
        }
      }
    }

    .custom-textarea__error {
      opacity: 1;

      @include vp-767 {
        bottom: -61px;
      }


      &-svg {
        position: absolute;
        right: 13px;
        bottom: 138px;

        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;

        background-color: $color-pumpkin;
        border-radius: 50%;
        cursor: pointer;

        @include vp-767 {
          right: 17px;
        }
      }
    }
  }
  /*&.is-valid {
    input {
      border: 2px solid $color-malachite;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-malachite, 0.5);
      }
    }
  }*/
}

.custom-textarea {
  position: relative;

  display: flex;

  label {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__error {
    &-description {
      position: absolute;
      top: calc(100% - 130px);
      right: calc(-50% + 27px);
      z-index: 1;

      width: 100%;

      text-align: center;

      opacity: 0;

      @include vp-767 {
        right: 0;
        left: 0;

        display: flex;
        justify-content: flex-end;
      }

      &::before {
        content: "";
        position: absolute;
        top: -16px;
        left: calc(50% - 6px);

        color: $color-default-white;

        border: 10px solid transparent;
        border-top: 10px solid $color-pumpkin;
        transform: rotate(180deg);

        @include vp-767 {
          left: calc(100% - 37px);
        }
      }
    }
  }
}

.custom-input__error-svg:hover + .custom-input__error-description {
  opacity: 1;
}

.custom-textarea__error-svg:hover + .custom-textarea__error-description {
  opacity: 1;
}
