.form {
  position: relative;

  padding-bottom: 123px;
  overflow: hidden;

  @include vp-767 {
    padding-bottom: 0;
  }

  &__bg--bottom {
    position: relative;

    img {
      position: absolute;
      right: 0;
      bottom: -123px;
      left: 0;

      width: 100%;
      max-width: 1440px;
      height: 250px;
      margin: 0 auto;

      @include vp-767 {
        display: none;
      }
      object-fit: cover;
    }
  }

  &__wrapper {
    position: relative;

    img {
      position: absolute;
      top: -50px;
      right: -178px;

      width: 176px;
      height: 552px;

      @include vp-1279 {
        display: none;
      }
    }
  }

  .title--h2 {
    max-width: 616px;
    margin-bottom: 16px;

    background: linear-gradient(264.68deg, $color-razzle-dazzle-rose -10.87%, $color-maya-blue 92.2%),
      linear-gradient(0deg, $color-default-white, $color-default-white);
    background-clip: text;

    @include vp-1023 {
      max-width: 581px;
    }

    @include vp-767 {
      margin-bottom: 4px;
    }
    -webkit-text-fill-color: transparent;
  }

  &__text {
    margin: 0;
    margin-bottom: 49px;

    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-align: center;

    @include vp-1023 {
      margin-bottom: 31px;

      font-size: 18px;
      line-height: 30px;
    }

    @include vp-767 {
      margin-bottom: 14px;
      padding: 0 20px;

      font-size: 16px;
      line-height: 24px;
    }
  }

  form {
    position: relative;
    z-index: 1;

    display: grid;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 48px 50px;

    color: $color-sapphire;

    background-color: $color-default-white;
    border-radius: 30px;

    grid-gap: 20px;

    @include vp-1023 {
      max-width: 668px;
    }

    @include vp-767 {
      max-width: 767px;
      padding: 15px 15px 30px;

      background-color: $color-default-white;
      border-radius: 0;

      grid-gap: 14px 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .custom-input:first-child {
    grid-column: 1/3;
  }

  .custom-textarea {
    grid-column: 1/3;

    &__label {
      margin-bottom: 6px;

      font-size: 14px;
      line-height: 20px;

      @include vp-767 {
        font-size: 12px;
        line-height: 17px;
      }
    }

    textarea {
      min-height: 150px;

      @include vp-767 {
        min-height: 110px;
      }
    }
  }

  .custom-input {
    display: flex;
    flex-direction: column;

    @include vp-767 {
      grid-column: 1;
    }

    &__label {
      margin-bottom: 6px;
    }

    label {
      font-size: 14px;
      line-height: 20px;

      @include vp-767 {
        font-size: 12px;
        line-height: 17px;

        input {
          min-height: 45px;
        }
      }
    }
  }

  .custom-input__variant {
    padding-left: 5px;

    color: rgba(0, 26, 84, 0.4);
  }

  .custom-toggle {
    color: rgba(0, 26, 84, 0.6);

    grid-column: 1/3;

    &__label {
      display: inline-block;

      font-size: 12px;
      line-height: 17px;

      @include vp-767 {
        font-size: 10px;
        line-height: 14.4px;
      }

      a {
        color: $color-royal-blue;
        transition: opacity $trans-600;

        &:hover {
          opacity: 0.6;
        }
      }
    }
  }

  &__btn {
    width: 100%;
    max-width: 106px;
    padding: 14.5px 0;

    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    color: $color-default-white;

    background-color: $color-royal-blue;
    border-radius: 100px;
    transition: background-color $trans-600;

    @include vp-767 {
      max-width: 100%;
      padding: 9.5px 0;
    }

    &:hover {
      background-color: $color-royal-blue2;
    }

    &:focus {
      background-color: $color-royal-blue2;
      box-shadow: inset 0 2px 2px $color-black-transparent;
    }
  }
}
