.scheme {
  display: grid;
  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  padding-bottom: 84px;

  grid-gap: 22px;
  grid-template-columns: 145px minmax(500px, 700px) 156px;

  @include vp-1439 {
    padding-bottom: 84px;
  }

  @include vp-1279 {
    max-width: 919px;
    padding-bottom: 66px;

    grid-gap: 16px;
  }

  @include vp-1023 {
    max-width: 582px;
    padding-bottom: 60px;

    grid-gap: 27px 40px;
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp-619 {
    max-width: 767px;
    padding: 0 0 30px;

    grid-gap: 7px 10px;
  }

  &__icon {
    position: absolute;

    display: flex;
    justify-content: center;

    background-color: $color-sapphire;

    @include vp-619 {
      background-color: inherit;
    }
  }

  &__highlighted-item {
    position: relative;

    @include vp-619 {
      min-height: auto;
      margin-top: 7px;
      padding: 3.5px 30px;

      background: $color-white-transparent;
      border-radius: 0 0 13% 13% / 0 0 100% 100%;
    }

    p {
      position: relative;
      z-index: 10;

      margin: 0;
      margin-top: -25px;

      font-weight: 800;
      font-size: 34px;
      line-height: 50px;
      text-transform: uppercase;
      letter-spacing: 0.05em;

      background: $portage-to-orchid;
      background-clip: text;

      @include vp-619 {
        margin: 0;

        font-weight: 700px;
        font-size: 13px;
        line-height: 18px;
        letter-spacing: 0.04em;

        -webkit-text-fill-color: $color-default-white;
      }
      -webkit-text-fill-color: transparent;
    }

    &::after {
      content: "";
      position: absolute;
      top: -10px;

      width: 120px;
      height: 50px;

      background-color: $color-sapphire;
      transform: translateX(-50%);

      @include vp-619 {
        display: none;
      }
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    text-align: center;

    @include vp-1279 {
      justify-content: flex-start;
    }

    @include vp-1023 {
      justify-content: flex-end;
      width: 100%;
    }

    svg {
      @include vp-619 {
        margin-bottom: 3px;
      }
      object-fit: cover;
    }
  }

  &__item-title {
    width: 100%;
    max-width: 189px;

    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    @include vp-619 {
      font-size: 14px;
      line-height: 14px;
    }
  }

  &__item-text {
    margin: 0;
    margin-top: 6px;

    font-size: 12px;
    line-height: 18px;

    opacity: 0.8;

    @include vp-619 {
      font-size: 11px;
      line-height: 17px;
    }
  }

  &__item--one {
    position: relative;

    align-self: flex-start;
    margin-top: 71px;

    grid-row-end: 3;
    grid-row-start: 1;

    @include vp-1279 {
      margin-top: 69px;
    }

    @include vp-1023 {
      max-width: 220px;
      margin: 0 0 0 auto;

      grid-row-end: auto;
    }

    @include vp-619 {
      max-width: 200px;
      margin: 0 auto;
      padding-left: 15px;
    }

    @include vp-479 {
      max-width: 140px;
      margin: 0 0 0 auto;
      padding-left: 0;
    }

    svg {
      @include vp-1279 {
        margin-top: 5px;
      }

      @include vp-1023 {
        height: 40px;
      }
    }

    .scheme__icon {
      right: -44px;

      width: 40px;
      height: 31px;

      transform: scale(1.1);

      @include vp-1279 {
        right: -39px;

        height: 41px;
      }

      @include vp-1023 {
        right: 50%;

        transform: translateX(50%) rotate(90deg);
      }

      @include vp-619 {
        right: 47%;
        z-index: 1;

        transform: translateX(50%) rotate(90deg) scale(0.8);
      }

      @include vp-479 {
        top: 99%;
        right: 50%;
      }

      svg {
        position: absolute;

        transform: rotate(-90deg);

        @include vp-1023 {
          height: 32px;
        }

        @include vp-619 {
          margin-top: 6px;
        }
      }
    }
  }

  &__item--two {
    align-self: flex-start;
    width: 100%;
    max-width: 700px;

    border: 2px solid $color-biloba-flower;
    border-radius: 50px;

    grid-row-start: 1;

    @include vp-1279 {
      max-width: 580px;
    }

    @include vp-1023 {
      grid-column: 1/3;
      grid-row-start: 2;
    }

    @include vp-619 {
      position: relative;

      max-width: 767px;
      margin: 0 auto;

      border: none;
    }

    .scheme__item-bg {
      display: none;

      @include vp-619 {
        position: absolute;
        top: 7px;
        right: 0;
        left: 0;

        display: block;
        margin: 0 auto;

        img {
          width: 100%;
          height: 303px;

          object-fit: cover;
        }
      }
    }
  }

  &__item--three {
    position: relative;

    align-self: flex-start;
    margin-top: 71px;

    grid-row-end: 3;
    grid-row-start: 1;

    @include vp-1279 {
      margin-top: 69px;

      svg {
        margin-top: 5px;
      }
    }

    @include vp-1023 {
      max-width: 220px;
      margin: 0 auto 0 0;

      grid-row-end: auto;
    }

    @include vp-619 {
      max-width: 200px;
      margin: 0 auto;
      padding-right: 15px;
    }

    @include vp-479 {
      max-width: 140px;
      margin: 0 auto 0 0;
      padding-right: 0;
    }

    .scheme__item-title {
      width: 100%;
      max-width: 156px;

      @include vp-479 {
        max-width: 81px;
      }
    }

    .scheme__icon {
      left: -43px;

      width: 40px;
      height: 31px;

      transform: scale(1.1);

      @include vp-1279 {
        left: -45px;

        height: 41px;
      }

      @include vp-1023 {
        bottom: 0;
        left: 50%;

        margin-top: 0;

        transform: translateX(-50%) rotate(-90deg);
      }

      @include vp-619 {
        left: 45%;

        transform: translateX(-50%) rotate(-90deg) scale(0.8);
      }

      @include vp-479 {
        left: 50%;
      }

      svg {
        position: absolute;

        transform: rotate(90deg);

        @include vp-1279 {
          width: 30px;
        }
      }
    }
  }

  &__item--four {
    position: relative;

    align-self: flex-start;
    width: 100%;
    max-width: 299px;
    margin: 0 auto;

    grid-column: 2/3;

    @include vp-1023 {
      grid-column: 1/3;
      grid-row-start: 3;
    }

    @include vp-619 {
      position: relative;

      max-width: 260px;
      margin-top: 14px;

      svg {
        display: none;
      }
    }

    .scheme__icon {
      top: -43px;
      left: 50%;

      width: 34px;
      height: 36px;

      transform: translateX(-50%) scale(1.1);

      @include vp-1279 {
        top: -38px;
      }

      @include vp-1023 {
        top: -44px;
      }

      @include vp-619 {
        top: -30px;
        z-index: 1;

        transform: translateX(-50%) scale(0.8);
      }

      svg {
        display: block;

        transform: rotate(-180deg);
      }
    }
  }

  &__columns {
    display: grid;
    margin: 0;
    margin-top: 15px;
    padding: 0 35px;

    list-style: none;

    grid-gap: 23px;
    grid-template-columns: 1fr 1fr;

    @include vp-1439 {
      margin-top: 13px;
    }

    @include vp-1279 {
      margin-top: 13px;

      grid-gap: 10px;
    }

    @include vp-1023 {
      margin-top: 16px;
      margin-bottom: 7px;
    }

    @include vp-619 {
      margin-top: 11px;
      margin-bottom: 12px;
      padding: 0 11px;
    }
  }

  .card-list {
    position: relative;

    margin-bottom: 47px;
    padding: 18px 24px 19px 24px;

    list-style: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-align: left;

    background: $color-white-transparent;
    border-radius: 20px;

    @include vp-479 {
      max-width: 146px;
    }

    @include vp-1279 {
      padding: 18px 21px 21px 21px;
    }

    @include vp-1023 {
      padding-bottom: 17px;

      font-size: 13px;
      line-height: 19px;
    }

    @include vp-619 {
      margin-bottom: 24px;
      padding: 14px 17px 14px 10px;

      font-size: 10px;
      line-height: 14px;

      background: linear-gradient(180deg, $color-resolution-blue3 0%, $color-jacksons-purple 100%);
      border-radius: 10px;
    }

    .scheme__icon--bottom {
      right: 50%;
      bottom: -41px;

      width: 40px;
      height: 36px;

      transform: translateX(50%);

      @include vp-1439 {
        bottom: -43px;
      }

      @include vp-1279 {
        bottom: -42px;
      }

      @include vp-1023 {
        bottom: -47px;
      }

      @include vp-619 {
        bottom: -36px;

        transform: translateX(50%) scale(0.8);
      }
    }

    &--one {
      @include vp-619 {
        background-color: inherit;
      }

      .scheme__icon--top {
        top: 42%;
        left: -50px;

        width: 30px;
        height: 36px;

        transform: rotate(-90deg) scale(1.1);

        @include vp-1279 {
          top: 37%;
        }

        @include vp-1023 {
          top: -33%;
          right: 0;
          left: 0;

          margin: 0 auto;

          transform: rotate(360deg);
        }

        @include vp-619 {
          top: -43%;

          transform: scale(0.8);
        }

        @include vp-479 {
          top: -30%;
        }
      }

      .card-list__bg {
        display: none;

        @include vp-619 {
          position: absolute;
          top: 0;
          right: 0;
          left: 0;

          display: block;
          width: 100%;
          height: 100%;
          min-height: 110px;
          margin: 0 auto;

          border-radius: 10px;

          img {
            width: 100%;
            height: 100%;

            border-radius: 10px;

            object-fit: cover;
          }
        }

        @include vp-479 {
          min-height: 166px;
        }
      }
    }

    &--two {
      .scheme__icon--top {
        top: 42%;
        right: -49px;

        width: 30px;
        height: 36px;

        transform: rotate(90deg) scale(1.1);

        @include vp-1279 {
          top: 37%;
        }

        @include vp-1023 {
          top: -33%;
          right: 0;
          left: 0;

          margin: 0 auto;

          transform: rotate(360deg);
        }

        @include vp-619 {
          top: -43%;

          transform: scale(0.8);
        }

        @include vp-479 {
          top: -30%;
        }
      }
    }
  }

  .card-list__sublist-item {
    position: relative;

    margin-bottom: 2px;
    padding-left: 15px;

    @include vp-1023 {
      margin-bottom: 4px;
    }

    @include vp-619 {
      margin-bottom: 3px;
      padding-left: 11px;
    }

    svg {
      position: absolute;
      top: 7px;
      left: 0;

      width: 7px;
      height: 7px;

      @include vp-619 {
        top: 5px;

        width: 3px;
        height: 4px;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__block {
    position: relative;

    width: 100%;
    max-width: 630px;
    margin: 0 auto 35px;
    padding: 13px 0 15px;

    background: $color-white-transparent;
    border-radius: 20px;

    @include vp-1279 {
      max-width: 510px;
    }

    @include vp-619 {
      max-width: 93%;
      margin-right: 11px;
      margin-bottom: 9px;
      margin-left: 11px;
      padding: 9px 0;

      background: none;
    }

    @include vp-479 {
      max-width: 298px;
    }
  }

  &__block-bg {
    display: none;

    @include vp-619 {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;

      display: block;
      width: 100%;
      height: 42px;
      margin: 0 auto;

      border-radius: 10px;

      img {
        width: 100%;
        height: 100%;

        border-radius: 10px;

        object-fit: cover;
      }
    }
  }

  &__block-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;

    @include vp-1023 {
      font-size: 18px;
      line-height: 27px;
    }

    @include vp-619 {
      position: relative;

      font-size: 10px;
      line-height: 13px;
    }
  }

  &__block-text {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;

    @include vp-1023 {
      font-size: 13px;
      line-height: 19.5px;
    }

    @include vp-619 {
      position: relative;

      font-size: 10px;
      line-height: 13px;
    }
  }
}
