.opportunities {
  position: relative;
  background-color: $color-sapphire-4;
  overflow: hidden;
}

.opportunities__img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 auto;
  min-width: 1440px;
  height: 160px;

  &--bottom {
    top: auto;
    bottom: -8px;
    height: 307px;
  }

  @include vp-1279 {
    &--bottom {
      bottom: -8px;
    }
  }

  @include vp-767 {
    min-width: 1100px;
    height: 122px;

    &--bottom {
      bottom: -6px;
      height: 234px;
    }
  }
}

.opportunities .container {
  padding: 105px 50px 130px;

  @include vp-1439 {
    padding: 75px 50px 110px;
  }

  @include vp-1279 {
    padding: 62px 89px 95px;
  }

  @include vp-1023 {
    padding: 61px 50px 78px;
  }

  @include vp-767 {
    padding: 26px 15px 20px;
  }
}

.opportunities__title {
  width: 603px;
  margin-bottom: 40px;

  @include vp-1279 {
    margin-bottom: 40px;
  }

  @include vp-1023 {
    width: 472px;
    margin-bottom: 27px;
    font-size: 36px;
    line-height: 46px;
  }

  @include vp-767 {
    width: 100%;
    margin-bottom: 15px;
    font-size: 22px;
    line-height: 30px;
    text-align: start;
  }
}

.opportunities__list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, auto);
  column-gap: 20px;
  row-gap: 20px;
  overflow: hidden;

  @include vp-1279 {
    column-gap: 18px;
    row-gap: 18px;
  }

  @include vp-1023 {
    column-gap: 19px;
    row-gap: 18px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
}

.opportunities__wrapper-image {
  position: absolute;
  width: 1903px;
  height: 767px;
  background: $opportunities-gradient;

  @include vp-1023 {
    width: 1480px;
  }

  @include vp-767 {
    display: none;
  }
}

.opportunities__item--1 .opportunities__wrapper-image {
  top: -44px;
  left: -105px;

  @include vp-1023 {
    left: -82px;
  }
}

.opportunities__item--2 .opportunities__wrapper-image {
  top: -44px;
  left: -505px;

  @include vp-1023 {
    left: -393px;
  }
}

.opportunities__item--3 .opportunities__wrapper-image {
  top: -44px;
  left: -905px;

  @include vp-1023 {
    height: 742px;
    left: -704px;
  }
}

.opportunities__item--4 .opportunities__wrapper-image {
  top: -314px;
  left: -105px;

  @include vp-1023 {
    height: 712px;
    top: -292px;
    left: -82px;
  }
}

.opportunities__item--5 .opportunities__wrapper-image {
  top: -314px;
  left: -505px;

  @include vp-1023 {
    height: 712px;
    top: -292px;
    left: -393px;
  }
}

.opportunities__list img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(80px);
  opacity: 0.4;

  @include vp-1023 {
    object-fit: fill;
    object-position: left;
  }

  @include vp-767 {
    display: none;
  }
}

.opportunities__list svg {
  position: relative;
  z-index: 10;

  @include vp-1023 {
    width: 30px;
    height: 30px;
  }

  @include vp-767 {
    min-width: 24px;
    min-height: 24px;
    width: 24px;
    height: 24px;
  }
}

.opportunities__item {
  position: relative;
  padding: 39px 32px 36px 40px;
  border-radius: 30px;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    border: 2px solid transparent;
    background: linear-gradient(161.07deg, rgba(255, 255, 255, 0.3) -213.89%, rgba(255, 255, 255, 0) 103.64%) border-box;
    mask: linear-gradient($color-default-white, $color-default-white) padding-box,
      linear-gradient($color-default-white, $color-default-white);
    mask-composite: exclude;
  }

  @include vp-1279 {
    padding: 29px 30px 31px;
  }

  @include vp-1023 {
    padding: 29px 22px 28px 30px;
  }

  @include vp-767 {
    display: flex;
    margin-bottom: 10px;
    padding: 0 9px 0 0;
    border-radius: 0;

    &::before {
      display: none;
    }

    & br {
      display: none;
    }
  }
}

.opportunities__item--3 {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: -1;
  padding-left: 42px;

  @include vp-1279 {
    padding-left: 32px;

    & br {
      display: none;
    }
  }

  @include vp-1023 {
    padding-left: 32px;
  }
}

.opportunities__item--4,
.opportunities__item--5 {
  padding-bottom: 40px;

  @include vp-1279 {
    padding-bottom: 29px;
  }

  @include vp-1023 {
    padding-bottom: 31px;
  }
}

.opportunities__description {
  position: relative;
  margin: 30px 0 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 34px;
  z-index: 10;

  @include vp-1279 {
    margin: 25px 0 0;
    font-size: 16px;
    line-height: 28px;
  }

  @include vp-1023 {
    margin: 26px 0 0;
    font-size: 14px;
    line-height: 24px;
  }

  @include vp-767 {
    margin: 0 0 0 9px;
    font-size: 12px;
    line-height: 20px;
  }
}

.opportunities__item--5 .opportunities__description {
  @include vp-767 {
    margin-top: 2px;
  }
}
