.cases {
  position: relative;
  margin-bottom: 45px;
  padding: 84px 0 96px;

  background-color: $color-sapphire-4;

  @include vp-1439 {
    margin-bottom: 17px;
    padding: 56px 0 63px;
  }

  @include vp-1279 {
    margin-bottom: 8px;
    padding: 60px 0 62px;
  }

  @include vp-1023 {
    margin-bottom: 0;
    padding: 43px 0 47px;
  }

  @include vp-767 {
    padding: 21px 0 31px;
  }

  &__title {
    margin-bottom: 31px;

    font-weight: 800;
    font-size: 46px;
    line-height: 69px;

    @include vp-1023 {
      margin-bottom: 23px;

      font-size: 36px;
      line-height: 54px;
    }

    @include vp-767 {
      margin-bottom: 11px;

      font-size: 22px;
      line-height: 33px;
      text-align: left;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(auto-fit, auto);
    gap: 0 20px;

    list-style: none;

    @include vp-767 {
      gap: 0;
      display: flex;
      align-items: center;
    }
  }

  &__item {
    position: relative;

    min-height: 421px;
    padding-bottom: 20px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    @include vp-1279 {
      min-height: 339px;
    }

    @include vp-1023 {
      min-height: 243px;
    }

    @include vp-767 {
      min-height: 274px;
    }
  }

  &__img {
    position: relative;

    width: 100%;
    min-height: 250px;
    margin-bottom: 31px;

    @include vp-1279 {
      min-height: 178px;
      margin-bottom: 25px;
    }

    @include vp-1023 {
      min-height: 140px;
      margin-bottom: 13px;
    }

    @include vp-767 {
      height: 100%;
      margin-bottom: 12px;
    }
  }

  &__link {
    width: 100%;
    height: auto;
    margin-bottom: 10px;

    transition: opacity $trans-600;

    @include vp-1023 {
      margin-bottom: 4px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-white;
        opacity: 0.7;

        transition: opacity $trans-600;
      }
    }
  }

  &__img-bg {
    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 30px;

    @include vp-1023 {
      border-radius: 15px;
    }
  }

  &__img img:last-of-type {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;

    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  &__img--souz img:last-of-type {
    width: 84%;
    height: 73%;

    object-fit: contain;
  }

  &__link-title {
    padding-left: 20px;

    font-weight: 800;
    font-size: 22px;
    line-height: 26px;

    text-align: left;

    @include vp-1279 {
      font-size: 20px;
    }

    @include vp-1023 {
      padding-left: 10px;
      font-size: 16px;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &__text {
    margin: 0;
    padding: 0 20px;

    font-weight: 500;
    font-size: 16px;
    line-height: 26px;

    opacity: 0.6;

    @include vp-1279 {
      font-size: 14px;
      line-height: 25px;
    }

    @include vp-1023 {
      padding: 0 10px;

      font-size: 12px;
      line-height: 20px;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  .swiper-pagination {
    display: none;
    bottom: 23px;

    @include vp-767 {
      display: block;
    }
  }

  .swiper-pagination-bullet {
    width: 7px;
    height: 7px;

    background-color: $color-default-white;
    opacity: 0.1;
  }

  .swiper-pagination-bullet-active {
    background-color: $yellow-green;
    opacity: 1;
  }
}
